import { graphql, useStaticQuery } from "gatsby"
import React, { useState } from "react"
import styled from "styled-components"
import Contact from "./kontakt"
import Logo from "../components/atoms/Logo"
import MainContent from "../components/molecules/MainContent"
import logo from "../assets/logo.svg"
import "bootstrap/dist/css/bootstrap.min.css"
import "../index.css"
import Gallery from "../components/organisms/Gallery"
import Seo from "../components/seo"
import Header from "../components/atoms/Header"
import background from "../assets/background.jpeg"

const StyledSection = styled.section`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
  padding: 150px 100px 100px;

  @media (max-width: 500px) {
    /* padding: 100px 50px 50px; */
    padding: 50px 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }
`

const StyledBackgroundWrapper = styled.div`
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  padding-bottom: 60px;
  background-image: url(${background});
  background-size: cover;
  transition: height 999999s;
  background-attachment: fixed;
`

const ContactSection = styled.section`
  background: white;
  padding-left: 20px;

  @media (max-width: 940px) {
    padding-left: 4px;
    padding-right: 4px;
  }
`

const Home = () => {
  const [activeMenu, setActiveMenu] = useState(false)

  const toggle = () => {
    setActiveMenu(x => !x)
  }

  const photos = useStaticQuery(graphql`
    {
      allFile(filter: { extension: { in: ["png", "jpg", "jpeg"] } }) {
        group(field: relativeDirectory) {
          relativeDirectory: fieldValue
          totalCount
          edges {
            node {
              relativePath
              publicURL
              absolutePath
              childImageSharp {
                small: fixed(height: 270) {
                  ...GatsbyImageSharpFixed
                }
                large: fixed(height: 600) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  `)
  return (
    <StyledBackgroundWrapper>
      <Seo title="Firma z drewna"></Seo>
      <Header activeMenu={activeMenu} toggle={toggle} />
      <StyledSection id="home">
        <MainContent />
        <Logo src={logo} alt="Logo" />
      </StyledSection>
      <StyledSection id="gallery">
        <Gallery
          photos={photos.allFile.group
            .filter(g =>
              ["laser", "gardenArchitecture", "decorations"].includes(
                g.relativeDirectory
              )
            )
            .flatMap(g => g.edges)}
          pageName="Galeria"
        />
      </StyledSection>
      <ContactSection id="contact">
        <Contact />
      </ContactSection>
    </StyledBackgroundWrapper>
  )
}

export default Home
