import React from "react"
import styled from "styled-components"
import ModalImage from "react-modal-image"

const StyledGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 270px));
  grid-gap: 15px;
  justify-content: center;
  @media (max-width: 772px) {
    grid-template-columns: 50% 50%;
  }
  > div {
    background-color: rgb(66, 69, 66, 0.4);
    margin: auto 0;
    border: 2px solid white;
    /* height: 270px; */
    border-radius: 5px;
    text-align: center;
    @media (max-width: 500px) {
      height: 50vw;
    }
    > img {
      margin-bottom: 0;
    }
  }
`

const Header = styled.div`
  color: white;
  font-size: 60px;
  font-family: "Poiret One", cursive;
  font-weight: 500;
  text-align: center;
  cursor: default;
  text-shadow: 2px 2px 2px black;
  margin-bottom: 10px;
`

const EmptyView = styled.h2`
  margin: auto;
  color: white;
  cursor: default;
  font-family: "Poiret One", cursive;
  font-size: 50px;
  font-weight: 600;
`

const Gallery = ({ photos, path, pageName }) => {
  return photos && photos.length > 0 ? (
    <div style={{ width: "100%" }}>
      <Header>{pageName}</Header>
      <StyledGrid>
        {photos.map((photo, index) => (
          <ModalImage
            key={index}
            hideDownload="true"
            hideZoom="true"
            small={photo.node.childImageSharp.small.src}
            large={photo.node.childImageSharp.large.src}
          />
        ))}
      </StyledGrid>
    </div>
  ) : (
    <EmptyView>Brak zdj</EmptyView>
  )
}

export default Gallery
