import React from "react"
import styled from "styled-components"
import Menu from "../molecules/Menu"
import logoText from "../../assets/logo-one-line-text.svg"
import Toggle from "./Toggle"
import LogoText from "./LogoText"

const HeaderContainer = styled.header`
  position: fixed;
  width: 100%;
  height: 60px;
  align-items: center;
  padding: 0 100px;
  display: flex;
  justify-content: space-between;
  background: white;
  z-index: 1000;

  @media (max-width: 755px) {
    padding: 0 10px;
    &.active {
      > div:before {
        top: 14px;
        transform: rotate(45deg);
        box-shadow: 0 0 0 #fff;
      }
      > div:after {
        bottom: 14px;
        transform: rotate(-45deg);
      }
    }
  }
`

const Header = ({ activeMenu, toggle }) => {
  return (
    <HeaderContainer className={activeMenu ? "active" : ""} id="header">
      <LogoText src={logoText} alt="Firma z drewna" to="/#home" />
      <Menu callback={() => toggle()} activeMenu={activeMenu} />
      <Toggle callback={() => toggle()} />
    </HeaderContainer>
  )
}

export default Header
